@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
* {
  margin: 0;
  font-family: "Quicksand";
  color: #333;
}

.navbar {
  padding: 20px;
  display: flex;
  align-items: center;
  max-width: 700px;
  margin: 0 auto;
  border-bottom: 1px solid #f2f2f2;
  color: #004da5;
}

.content {
  margin-top: 15px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

input {
  width: 50%;
  padding: 6px 10px;
  margin: 10px 0;
  border: 1px solid #ddd;
  box-sizing: border-box;
  display: block;
  font-size: 18px;
}

button {
  background: #004da5;
  color: #fff;
  border: 0;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
  width: 15%;
  font-size: 15px;
}

.error {
  margin-top: 20px;
  font-size: 18px;
  color: red;
}
.card {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    max-width: 300px;
    height: 670px;
    margin: auto;
    text-align: center;
    margin-bottom: 10px;
}

p {
    color: grey;
    font-size: 17px;
}

h2 {
    margin: 10px
}

.description {
    padding: 4px;
}

.list {
    display: flex;
    flex-direction: row;
    padding: 5px;
    flex-wrap: wrap;
}

img {
    width: 300px;
    height: 445px;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

